import {
  CButton,
  CCard,
  CCardHeader,
  CCol,
  CRow,
  CTooltip,
  CInput,
  CForm,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CFormGroup,
  CLabel,
  CSelect,
  CCardBody,
  CTextarea,
} from "@coreui/react";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { CardBody } from "reactstrap";
import MaterialTable from "material-table";
import CIcon from "@coreui/icons-react";
import Select from "react-select";
import makeAnimated from "react-select/animated";

// import all action redux
import {
  setFormPertanyaanAction,
  GetPertanyaansAction,
  AddPertanyaanAction,
} from "src/common/redux/action";


const Pertanyaan = () => {

   const animatedComponents = makeAnimated();


  const {
    GetListPertanyaanReducer,
    modalPertanyaan,
    formPertanyaanReducer,
  } = useSelector((state) => state.masterPertanyaanReducer);

  const {
    IdPertanyaan,
    Pertanyaan,
    BobotDefault,
  } = formPertanyaanReducer;

  const dispatch = useDispatch();
      const [stateUpdate, setStateUpdate] = useState(false);

   const startLoad = () => {
     dispatch(GetPertanyaansAction("GET_LIST_PERTANYAAN"));
   };


  // set action
   const showModal = () =>
     dispatch({ type: "MODAL_PERTANYAAN", payload: !modalPertanyaan });


  const handleModalPertanyaan = (type, idPertanyaan) => {
    if (type === "add") {
      showModal();
      dispatch({ type: "RESET_FORM_PERTANYAAN" });

    } else {
    }
  };

  const prosesPertanyaanAdd = () =>{
    dispatch(AddPertanyaanAction(formPertanyaanReducer));
  }
  const prosesPertanyaanEdit =() =>{}

  const prosesDelete = () => {}



  useEffect(() => {
    startLoad()
  },[dispatch])

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <CTooltip content="Edit Pertanyaan">
              <button
                type="button"
                className="btn btn-sm btn-dark text-center mr-2"
                onClick={() =>
                  handleModalPertanyaan("edit", rowData.idPertanyaan)
                }
              >
                <CIcon name="cil-pencil" /> Update
              </button>
            </CTooltip>
            <CTooltip content="Hapus Pertanyaan">
              <button
                type="button"
                className="btn btn-sm btn-danger"
                onClick={() => prosesDelete(rowData.idPertanyaan, rowData.nama)}
              >
                <CIcon name="cil-drop" /> Hapus
              </button>
            </CTooltip>
          </div>
        );
      },
    },
    { title: "Indikator", field: "indikator" },
    { title: "Pertanyaan", field: "pertanyaan" },
    {
      title: "Bobot Default",
      field: "bobotDefault",
      render: (rowData) =>
        rowData.bobotDefault === null ? (
          <span className="badge badge-dark">0</span>
        ) : (
          <span className="badge badge-danger">{rowData.bobotDefault}</span>
        ),
    },
  ];

  return (
    <div>
      <CRow>
        <CCol md="12">
          <CCard>
            <CCardHeader>
              <CRow>
                <CCol md="3">
                  <CRow>
                    <CCol md="6">
                      <CButton
                        color="primary"
                        onClick={() => handleModalPertanyaan("add")}
                      >
                        <FontAwesomeIcon icon="plus" />
                        Tambah
                      </CButton>
                    </CCol>
                  </CRow>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <MaterialTable
                title=""
                options={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={GetListPertanyaanReducer}
              />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>

      <CModal show={modalPertanyaan} onClose={showModal} size="lg">
        <CModalHeader closeButton>
          <CModalTitle>
            {" "}
            {stateUpdate ? "Form Update" : "Form Tambah"}
          </CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm>
            <CFormGroup>
              <CLabel htmlFor="nf-nama">Nama</CLabel>
              <CTextarea
                name="pertanyaan"
                placeholder="Input Pertanyaan ...!"
                value={Pertanyaan}
                onChange={(e) =>
                  dispatch(
                    setFormPertanyaanAction("Pertanyaan", e.target.value)
                  )
                }
              />
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-nama">Bobot Default</CLabel>
              <CInput
                type="number"
                id="nf-bobotdefault"
                name="nama"
                placeholder="input nama Bobot ..."
                value={BobotDefault}
                onChange={(e) =>
                  dispatch(
                    setFormPertanyaanAction("BobotDefault", e.target.value)
                  )
                }
              />
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={stateUpdate ? prosesPertanyaanEdit : prosesPertanyaanAdd}
          >
            {stateUpdate ? "Update" : "Tambah"}
          </CButton>
          <CButton color="secondary" onClick={showModal}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </div>
  );
};

export default Pertanyaan;
